import React, { useEffect } from 'react';
import './Aboutme.scss';
import content from './assets/img/works.png';

function Works(props) {

  useEffect ( () => {
    props.setmenuStyles(3);
  });

  return (
    <div className="Works image">
        <img src={props.worksphoto }/>
    </div>
  );
}

export default Works;